import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { MyTripsComponent } from './my-trips.component';

const routes: Routes = [
    {
        path: '',
        component: MyTripsComponent,
        children: [
            {
                path: 'bookings-list',
                loadChildren: () => import('./bookings/bookings.module').then((m) => m.BookingsListModule),
            },
            {
                path: 'booking-summary/:id',
                loadChildren: () => import('./booking/booking.module').then((m) => m.BookingSummaryModule),
            },
            {
                path: 'accommodation-booking/:id',
                loadChildren: () =>
                    import('./accommodation-booking/accommodation-booking.module').then(
                        (m) => m.AccommodationBookingModule
                    ),
            },
            {
                path: '',
                pathMatch: 'full',
                redirectTo: 'bookings-list',
            },
            {
                path: '**',
                redirectTo: 'bookings-list',
            },
        ],
    },
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule],
})
export class BookingsRoutingModule {}
